@import url("normalize.css");
@font-face {
  font-family: 'Bebas Neue';
  font-weight: 300;
  font-style: normal;
  font-display: swap;
  src: local('Bebas Neue Book'), local('BebasNeueBook'), url('../fonts/bebasneuebook.woff2') format('woff2'), url('../fonts/bebasneuebook.woff.woff') format('woff');
}
@font-face {
  font-family: 'Bebas Neue';
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: local('Bebas Neue Regular'), local('BebasNeueRegular'), url('../fonts/bebasneueregular.woff2') format('woff2'), url('../fonts/bebasneueregular.woff') format('woff');
}
@font-face {
  font-family: 'Montserrat';
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: local('Montserrat Regular'), local('Montserrat-Regular'), url('../fonts/Montserrat-Regular.woff2') format('woff2'), url('../fonts/Montserrat-Regular.woff') format('woff');
}
@font-face {
  font-family: 'Montserrat';
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src: local('Montserrat Medium'), local('Montserrat-Medium'), url('../fonts/Montserrat-Medium.woff2') format('woff2'), url('../fonts/Montserrat-Medium.woff') format('woff');
}
@media (min-width: 1440px) {
  .page__container {
    display: flex;
    justify-content: flex-end;
    flex-wrap: wrap;
  }
}
.container {
  width: 280px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .container {
    width: 708px;
  }
}
@media (min-width: 1440px) {
  .container {
    width: 1395px;
  }
}
@media (min-width: 1920px) {
  .container {
    width: 1860px;
  }
}
.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  padding: 0;
  border: 0;
  height: 1px;
  width: 1px;
  white-space: nowrap;
  clip: rect(1px, 1px, 1px, 1px);
  clip-path: inset(100%);
  overflow: hidden;
}
.page {
  height: 100%;
  min-width: 320px;
  font-family: 'Montserrat', 'Arial', sans-serif;
  font-weight: normal;
  font-size: 18px;
  line-height: 1.55555556;
  background-color: #fff;
  background-image: url("../img/background@1x.jpg");
  background-repeat: no-repeat;
  background-position: top center;
  background-size: cover;
}
@media (min-resolution: 2dppx) {
  .page {
    background-image: url("../img/background@2x.jpg");
  }
}
.page__img-wrapper {
  display: none;
  position: absolute;
}
@media (min-width: 768px) {
}
.page__img {
  width: 240px;
}
@media (min-width: 1440px) {
  .page__img {
    width: 731px;
  }
}
.page__img--add {
  position: absolute;
  z-index: 5;
  top: 937px;
  left: 405px;
}
@media (max-width: 1440px) {
  .page__img--add {
    width: 93px;
    top: 712px;
    left: 320px;
  }
}
.page__nav {
  flex-direction: column;
  flex-grow: 0;
  flex-shrink: 0;
  flex-basis: 100%;
}
@media (min-width: 1440px) {
  .page__nav {
    flex-basis: 712px;
  }
}
@media (min-width: 1920px) {
  .page__nav {
    flex-basis: 950px;
  }
}
.nav {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.nav__toggle {
  position: absolute;
  top: 0;
  right: 0;
  padding: 0;
  outline: none;
  border: none;
  cursor: pointer;
  z-index: 2;
  transition: 0.5s;
}
@media (min-width: 768px) {
  .nav__toggle {
    display: none;
  }
}
@media (min-width: 1440px) {
  .nav {
    padding-top: 42px;
    padding-bottom: 42px;
  }
}
@media (min-width: 1920px) {
  .nav {
    padding-top: 58px;
    padding-bottom: 59px;
  }
}
.nav__item {
  display: block;
  width: 100%;
  padding-top: 7px;
  padding-bottom: 7px;
  text-decoration: none;
  text-transform: uppercase;
  font-family: 'Bebas Neue', 'Arial', sans-serif;
  font-size: 20px;
  text-align: center;
  color: #1d1d1b;
  border-bottom: 1px solid #5a8a1d;
}
.nav__item:nth-last-child(2) {
  border: none;
}
@media (min-width: 1440px) {
  .nav__item {
    font-size: 18px;
  }
}
@media (min-width: 1920px) {
  .nav__item {
    font-size: 24px;
  }
}
.nav__item--tel {
  display: block;
  border: 1px solid #c7af8a;
  padding-top: 5px;
  padding-bottom: 5px;
  padding-left: 13px;
  padding-right: 15px;
  border-radius: 10px;
  line-height: 1.2;
}
@media (min-width: 1440px) {
  .nav__item--tel {
    padding-left: 11px;
    padding-right: 10px;
  }
}
@media (max-width: 320px) {
  .nav--closed .nav__toggle::before {
    content: "";
    position: absolute;
    top: 14px;
    right: 0;
    width: 24px;
    height: 2px;
    z-index: 10;
    background-color: #1d1d1b;
    box-shadow: 0 8px 0 0 #1d1d1b, 0 16px 0 0 #1d1d1b;
  }
  .nav--closed .nav__toggle:active::before {
    background-color: rgba(29, 29, 27, 0.3);
    z-index: 10;
    box-shadow: 0 8px 0 0 rgba(29, 29, 27, 0.3), 0 16px 0 0 rgba(29, 29, 27, 0.3);
  }
  .nav--opened .nav__toggle::before,
  .nav--opened .nav__toggle::after {
    content: "";
    position: absolute;
    top: 20px;
    right: -2px;
    width: 24px;
    height: 2px;
    background-color: #1d1d1b;
  }
  .nav--opened .nav__toggle::before {
    transform: rotate(45deg);
    box-shadow: none;
  }
  .nav--opened .nav__toggle::after {
    transform: rotate(-45deg);
  }
  .nav--opened .nav__toggle:active::before,
  .nav--opened .nav__toggle:active::after {
    background-color: rgba(29, 29, 27, 0.3);
  }
  .nav--closed .nav__item {
    display: none;
  }
  .nav--nojs .nav__toggle {
    display: none;
  }
  .nav--nojs .nav__wrapper {
    position: static;
    min-height: 0;
  }
  .nav--nojs .nav__item {
    display: block;
  }
}
