.form {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding-left: 25px;
  padding-right: 25px;
  padding-bottom: 10px;
  box-sizing: border-box;
  background-color: #d3c1a2;
  outline: 3px solid #c5ac84;
  outline-offset: 3px;
}
.form::before {
  content: "";
  position: absolute;
  width: 280px;
  height: 22px;
  background-image: url("../img/decor-up.svg");
  background-size: cover;
  top: -26px;
  left: 0px;
}
.form::after {
  content: "";
  position: absolute;
  width: 280px;
  height: 22px;
  background-image: url("../img/decor-up.svg");
  background-size: cover;
  transform: rotate(180deg);
  top: 260px;
  left: 0;
}
@media (min-width: 1440px) {
  .form {
    width: 1100px;
    height: 48px;
    padding-right: 326px;
    padding-left: 86px;
  }
}
@media (min-width: 1920px) {
  .form {
    left: 30px;
    padding-left: 58px;
    padding-right: 425px;
    width: 1400px;
    height: 64px;
  }
  .form::before {
    content: "";
    position: absolute;
    width: 508px;
    height: 44px;
    background-image: url("../img/decor-up.svg");
    top: -43px;
    left: 289px;
  }
  .form::after {
    content: "";
    position: absolute;
    width: 508px;
    height: 44px;
    background-image: url("../img/decor-up.svg");
    transform: rotate(180deg);
    top: 62px;
    left: 289px;
  }
}
.form__title {
